import {
  Card,
  Breadcrumb,
  Form,
  Button,
  Radio,
  Input,
  Upload,
  Space,
  Select,
  message
} from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import './index.scss'
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
import { useStore } from '@/store'
import { useEffect, useRef, useState } from 'react'
import { http } from '@/utils'



const Publish = () => {

  const { Option } = Select
  //
  const { channelStore } = useStore()


  //
  //fileListurlfileListupload
  //actionresresfileListfileListurl
  //fileList<upload>fileList
  const [fileList, setFileList] = useState([])

  const onUploadChange = ({ fileList }) => { //resfileList
    console.log(fileList)
    const formatList = fileList.map(file => {
      //
      if (file.response) {
        return {
          url: file.response.url //responseurl,
        }
      }
      //
      return file
    })
    console.log(formatList)
    setFileList(formatList)
  }


  ///
  const [imaCount, setImaCount] = useState(1)
  const radioChange = (e) => {
    //console.log(e)
    setImaCount(e.target.value)
  }


  //
  const navigate = useNavigate()
  const onFinish = async (values) => { //valuesname
    const { channel_id, content, title } = values
    const params = {
      channel_id: channel_id,
      content: content,
      title: title,
      images: JSON.stringify(fileList) //fileList
    }
    //console.log(params)

    if (articleId) {
      //
      await http.put(`/my/article/edit/${articleId}`, { ...params, id: articleId })
    } else {
      //
      await http.post('/my/article/add', params)
    }

    ///
    navigate('/article')
    message.success(`${articleId ? 'Update success' : 'Publish success'}`) //messageantd

  }


  //idor
  const [params] = useSearchParams()
  const articleId = params.get('id')

  //id1.2.uploadfileList
  const form = useRef(null) //Form DOM,ref

  useEffect(() => {
    const loadDetail = async () => {
      const res = await http.get(`/my/article/${articleId}`)
      console.log(res)
      const { images, ...formValue } = res.data.data
      form.current.setFieldsValue({ ...formValue })//
      //fileList
      setFileList(JSON.parse(images))
      console.log(JSON.parse(images))
    }
    if (articleId) {
      loadDetail()
    }
  }, [articleId])



  return (
    <div className="publish">
      <Card
        title={
          <Breadcrumb separator=">">
            <Breadcrumb.Item>
              <Link to="/">Home</Link>
            </Breadcrumb.Item>
            <Breadcrumb.Item>{articleId ? 'Update' : 'Publish'}</Breadcrumb.Item>
          </Breadcrumb>
        }
      >
        <Form
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 16 }}
          initialValues={{ type: 1 }}
          onFinish={onFinish}
          ref={form} //refform<Form/>form<Form/>
        >
          <Form.Item
            label="Title"
            name="title"
            rules={[{ required: true, message: 'Please input a title' }]}
          >
            <Input placeholder="Please input a title" style={{ width: 400 }} />
          </Form.Item>
          <Form.Item
            label="Category"
            name="channel_id"
            rules={[{ required: true, message: 'Please select a category' }]}
          >
            <Select placeholder="Please select a category" style={{ width: 400 }}>
              {channelStore.channelList.map(item => (
                <Option key={item.id} value={item.id}>{item.name}</Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item label="Cover">
            {/* <Form.Item/>Radio.Group,Radioupload */}
            <Form.Item name="type">
              <Radio.Group onChange={radioChange}>
                <Radio value={1}>upload cover</Radio>
                <Radio value={0}>No cover</Radio>
              </Radio.Group>
            </Form.Item>
            {/* UploadUpload<Form.Item label='' > */}
            {imaCount > 0 && (
              <Upload
                name="images"
                listType="picture-card"
                className="avatar-uploader"
                showUploadList
                action="https://blog-yy-server.herokuapp.com/api/upload"
                fileList={fileList}
                onChange={onUploadChange} //
                // multiple={imaCount > 1}
                maxCount='1'
              >
                <div style={{ marginTop: 8 }}>
                  <PlusOutlined />
                </div>
              </Upload>
            )}
          </Form.Item>

          <Form.Item
            label="Content"
            name="content"
            initialValue=''
            rules={[{ required: true, message: 'Please input your diary' }]}
          >
            <ReactQuill
              className="publish-quill"
              theme="snow"
              placeholder="Input your diary here"
            />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 4 }}>
            <Space>
              <Button size="large" type="primary" htmlType="submit">
                {articleId ? 'Update' : 'Publish'}
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default observer(Publish)